import React, { useState, useEffect } from 'react';

const ImageSlider = ({ images }) => {
  const [currentImage, setCurrentImage] = useState(0);
  const [widthPercentage, setWidthPercentage] = useState(0);

  const handleThumbnailClick = (index) => {
    setCurrentImage(index);
    resetProgressBar();
  };

  const handleBackButtonClick = () => {
    setCurrentImage((prev) => (prev - 1 + images.length) % images.length);
    resetProgressBar();
  };

  const handleNextButtonClick = () => {
    setCurrentImage((prev) => (prev + 1) % images.length);
    resetProgressBar();
  };

  const resetProgressBar = () => {
    setWidthPercentage(0);
  };

  useEffect(() => {
    let interval;
    let startTime;

    const startSlider = () => {
      startTime = Date.now();
      interval = setInterval(() => {
        const elapsedTime = Date.now() - startTime;
        setWidthPercentage((elapsedTime / 5000) * 100);
      }, 10);
    };

    startSlider();

    return () => {
      clearInterval(interval);
    };
  }, [currentImage, images]);

  useEffect(() => {
    if (widthPercentage >= 100) {
      resetProgressBar();
      setCurrentImage((prev) => (prev + 1) % images.length);
    }
  }, [widthPercentage, images]);

  useEffect(() => {
    // 이미지 변경 시 progress-bar 초기화
    resetProgressBar();
  }, [currentImage]);

  return (
    <div className="image-slider">
      <div className="images">
        {images.map((image, index) => (
          <img key={index} src={image} alt={`images-${index}`} className={index === currentImage ? 'active' : ''} />
        ))}
      </div>
      <div className="thumbnails">
        {images.map((image, index) => (
          <img
            key={index}
            src={image}
            alt={`thumbnail-${index}`}
            className={index === currentImage ? 'active' : ''}
            onClick={() => handleThumbnailClick(index)}
          />
        ))}
      </div>
      <div className="progress-bar-container">
        <div className="progress-bar" style={{ width: `${widthPercentage}%` }}></div>
      </div>
      <div className="back-btn" onClick={handleBackButtonClick}>
        <ion-icon name="chevron-back-outline"></ion-icon>
      </div>
      <div className="next-btn" onClick={handleNextButtonClick}>
        <ion-icon name="chevron-forward-outline"></ion-icon>
      </div>
    </div>
  );
};

const HighlightListItem = ({ onClick }) => {

  const [isHovered, setIsHovered] = useState(false); // 마우스 호버 여부

  // 마우스가 요소에 진입 시
  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  // 마우스가 요소를 벗어날 시
  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  // 마우스로 요소를 클릭 시
  const handleClick = () => {
    onClick();
  };

  const className = isHovered ? 'highlight' : '';

  return (
    <li
      className={className}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onClick={handleClick}
    ></li>
  );
};

const VideoModal = ({ isOpen, videos, selectedVideoIndex, onClose }) => {

  if (!isOpen) {
    return null;
  }

  return (
    <div className='video-box'>
      <button className='close-buttons' onClick={onClose}>X</button>
      <video controls autoPlay>
        <source src={process.env.PUBLIC_URL + videos[selectedVideoIndex]} type="video/mp4" />
        Your browser does not support the video tag.
      </video>
    </div>
  );
};

const Page3 = () => {

  let images = [
    "https://i.postimg.cc/jjsjgv5z/peakpx-4.jpg",
    "https://i.postimg.cc/T2Spdr76/peakpx-5.jpg",
    "https://i.postimg.cc/8cq5ZSJ5/peakpx-1.jpg",
    "https://i.postimg.cc/0y1jm91X/peakpx-3.jpg",
    "https://i.postimg.cc/hPdhff67/peakpx-2.jpg",
    "https://i.postimg.cc/NFyF8M0S/peakpx.jpg",
  ];

  let videos = [
    "/video/avatar_video.mp4",
    "/video/avatar_video.mp4",
    "/video/avatar_video.mp4",
    "/video/avatar_video.mp4",
  ];

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedVideoIndex, setSelectedVideoIndex] = useState(0);
  const itemsArray = Array.from({ length: 4 }, (_, index) => index);

  const handleHighlightItemClick = (index) => {
    setIsModalOpen(true);
    setSelectedVideoIndex(index);
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <section className='page3'>
        <article className='image-slider'>
          <ImageSlider images={images} />
        </article>
        <div className='video-container'>
          <ul className={`videos-modal ${isModalOpen ? 'modal-open' : ''}`}>
            <VideoModal
              isOpen={isModalOpen}
              videos={videos}
              selectedVideoIndex={selectedVideoIndex}
              onClose={handleModalClose}
            />
          </ul>
          <ul className='video-slider'>
            {itemsArray.map((_, index) => (
              <HighlightListItem
                key={index}
                onClick={() => handleHighlightItemClick(index)}
              />
            ))}
          </ul>
        </div>
      </section>
    </>
  );
};

export default Page3;
