import React, { useState, useEffect, useRef } from 'react';
import { useScript } from "./hooks";

const Page2 = () => {

  const status = useScript("https://unpkg.com/splitting@1.0.6/dist/splitting.min.js");
  useEffect(() => {
    if (status === "ready") {
      window.Splitting({
        whitespace: true
      })
    }
  }, [status])

  const techs = [
    'Avatar2',
    'Underwater',
    'Avatar2',
    'Underwater',
    'Avatar2',
    'WetaFX',
    'Avatar2',
    'WetaFX',
    'Avatar2',
    'WetaFX',
    'Avatar2',
  ];

  const imageUrls = [
    'tech/tech01.jpg',
    'tech/tech02.jpg',
    'tech/tech03.jpg',
    'tech/tech04.jpg',
    'tech/tech05.jpg',
    'tech/tech06.jpg',
    'tech/tech07.jpg',
    'tech/tech08.jpg',
    'tech/tech09.jpg',
    'tech/tech10.jpg',
  ];

  const [progress, setProgress] = useState(50);
  const startX = useRef(0);
  const active = useRef(0);
  const isDown = useRef(false);

  const speedWheel = 0.02;
  const speedDrag = -0.1;

  const itemsRef = useRef([]);

  const getZindex = (array, index) =>
    array.map((_, i) => (index === i ? array.length : array.length - Math.abs(index - i)));

  useEffect(() => {
    const displayItems = (item, index) => {
      const zIndex = getZindex(itemsRef.current, active.current)[index];
      item.style.setProperty('--zIndex', zIndex);
      item.style.setProperty('--active', (index - active.current) / itemsRef.current.length);
    };

    const animate = () => {
      setProgress((prevProgress) => Math.max(0, Math.min(prevProgress, 100)));
      active.current = Math.floor((progress / 100) * (itemsRef.current.length - 1));

      itemsRef.current.forEach((item, index) => displayItems(item, index));
    };

    animate();
  }, [progress]);

  const handleItemClick = (i) => {
    setProgress((i / itemsRef.current.length) * 100 + 10);
  };

  const handleWheel = (e) => {
    const wheelProgress = e.deltaY * speedWheel;
    setProgress((prevProgress) => prevProgress + wheelProgress);
  };

  const handleMouseMove = (e) => {

    if (!isDown.current) return;

    const x = e.clientX || (e.touches && e.touches[0].clientX) || 0;
    const mouseProgress = (x - startX.current) * speedDrag;

    setProgress((prevProgress) => prevProgress + mouseProgress);
    startX.current = x;
  };

  const handleMouseDown = (e) => {
    isDown.current = true;
    startX.current = e.clientX || (e.touches && e.touches[0].clientX) || 0;
  };

  const handleMouseUp = () => {
    isDown.current = false;
  };

  return (
    <>
      <section className='carousel' onWheel={handleWheel}>
        {Array(10).fill().map((_, index) => (
          <article
            className="carousel-item"
            key={index}
            ref={(el) => (itemsRef.current[index] = el)}
            onClick={() => handleItemClick(index)}
            onMouseDown={handleMouseDown}
            onMouseMove={handleMouseMove}
            onMouseUp={handleMouseUp}
            onTouchStart={handleMouseDown}
            onTouchMove={handleMouseMove}
            onTouchEnd={handleMouseUp}
          >
            <div className="carousel-box">
              <h2 className="title">{techs[index]}</h2>
              <p className="num">{String(index + 1).padStart(2, '0')}</p>
              <img
                src={process.env.PUBLIC_URL + '/' + imageUrls[index]}
                alt={techs[index] + 'image'}
              />
            </div>
          </article>
        ))}
      </section>

      <div className="layout">
        <div className="box">High-end, full-service<br />visual content creation<br />for webstyle branding.</div>
      </div>
      <p className='wave-text' data-splitting=''>Just Avatar2 web</p>
      <p className='wave-text1' data-splitting=''>Just Avatar2 web</p>
    </>
  )
}

export default Page2;