import React, { useEffect, useRef, useState, useMemo } from 'react';
import $ from 'jquery';

const FPS = 10;
const DURATION = 300;
const CHARACTERS = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
const DELAY = ~~(300 / FPS);
const FRAME_COUNT = ~~(DURATION / 300) * FPS;

const Home = () => {
  const visualRef = useRef(null);
  const modalRef = useRef(null);
  const avatarRef = useRef(null);
  const wayOfWaterRef = useRef(null);
  const [backgroundIndex, setBackgroundIndex] = useState(0);

  const images = useMemo(() => [
    'https://i.postimg.cc/5jcjNVmz/bg03.jpg',
    'https://i.imgur.com/6f4wdVr.jpg',
    'https://i.postimg.cc/DZx05SRg/background01.jpg',
  ], []);

  useEffect(() => {

    const backgroundChangeTimer = setInterval(() => {
      setBackgroundIndex((prevIndex) => (prevIndex + 1) % images.length);
    }, 6000);

    // 컴포넌트가 unmount될 때 타이머 해제
    return () => clearInterval(backgroundChangeTimer);
  }, [images]);

  useEffect(() => {
    const avatarElement = avatarRef.current;
    const wayOfWaterElement = wayOfWaterRef.current;

    let avatarFrameIndex = 0;
    let wayOfWaterFrameIndex = 0;
    let avatarTimeoutId = undefined;
    let wayOfWaterTimeoutId = undefined;

    function resetText(element, timeoutId, frameIndex) {
      if (timeoutId !== undefined) clearTimeout(timeoutId);
      frameIndex = 0;
      element.innerText = element.dataset.text;
    }

    function setRandomText(element, frameIndex) {
      const text = Array.from({ length: element.dataset.text.length }).map(() => CHARACTERS[~~(Math.random() * CHARACTERS.length)]);
      element.innerText = text.join('');
    }

    function animate(element, frameIndex, timeoutId, dataText) {
      if (frameIndex >= FRAME_COUNT) {
        resetText(element, timeoutId, frameIndex);
      } else {
        frameIndex += 1;
        setRandomText(element, frameIndex);
        timeoutId = setTimeout(() => animate(element, frameIndex, timeoutId, dataText), DELAY);
      }
    }

    avatarElement.addEventListener('mouseenter', () => animate(avatarElement, avatarFrameIndex, avatarTimeoutId, avatarElement.dataset.text));
    avatarElement.addEventListener('mouseout', () => resetText(avatarElement, avatarTimeoutId, avatarFrameIndex));

    wayOfWaterElement.addEventListener('mouseenter', () => animate(wayOfWaterElement, wayOfWaterFrameIndex, wayOfWaterTimeoutId, wayOfWaterElement.dataset.text));
    wayOfWaterElement.addEventListener('mouseout', () => resetText(wayOfWaterElement, wayOfWaterTimeoutId, wayOfWaterFrameIndex));

    resetText(avatarElement, avatarTimeoutId, avatarFrameIndex);
    resetText(wayOfWaterElement, wayOfWaterTimeoutId, wayOfWaterFrameIndex);

    return () => {
      avatarElement.removeEventListener('mouseenter', () => animate(avatarElement, avatarFrameIndex, avatarTimeoutId, avatarElement.dataset.text));
      avatarElement.removeEventListener('mouseout', () => resetText(avatarElement, avatarTimeoutId, avatarFrameIndex));

      wayOfWaterElement.removeEventListener('mouseenter', () => animate(wayOfWaterElement, wayOfWaterFrameIndex, wayOfWaterTimeoutId, wayOfWaterElement.dataset.text));
      wayOfWaterElement.removeEventListener('mouseout', () => resetText(wayOfWaterElement, wayOfWaterTimeoutId, wayOfWaterFrameIndex));
    };
  }, []);

  const openModal = () => {
    const videoElement = $(modalRef.current).find('video')[0];
    videoElement.play();

    $(modalRef.current).css({
      'display': 'block',
      'opacity': '0',
    });

    setTimeout(() => {
      $(modalRef.current).css('opacity', '1');
    }, 0);
  };

  const closeModal = () => {
    const videoElement = $(modalRef.current).find('video')[0];
    videoElement.pause();
    videoElement.currentTime = 0;

    $(modalRef.current).css('opacity', '0');
    setTimeout(() => {
      $(modalRef.current).hide();
    }, 300);
  };

  return (
    <>
      <style>
        {`
          .visual {
            background-image: url(${images[backgroundIndex]});
          }
        `}
      </style>

      <main className='visual' ref={visualRef}>
        <div className='video-modal' ref={modalRef}>
          <div className='video-modal-content'>
            <div className='video-box'>
              <video src={process.env.PUBLIC_URL + '/video/avatar_video.mp4'} controls />
              <button className='close-button' onClick={closeModal}>X</button>
            </div>
          </div>
        </div>

        <div className='container'>
          <div className='content-box'>
            <div className='inner'>
              <img src="https://i.imgur.com/6f4wdVr.jpg" onClick={openModal} alt="images" />
            </div>
            <h2>
              <p ref={avatarRef} data-text="AVATAR"></p>
              <p ref={wayOfWaterRef} data-text="THE WAY OF WATER"></p>
            </h2>
          </div>
        </div>
      </main>
    </>
  );
}

export default Home;
