import React from 'react';

const Page1 = () => {
  const satelliteData = [
    { imagePath: 'cast/cast00.jpeg', text: 'Jake Sully' },
    { imagePath: 'cast/cast01.jpeg', text: 'Neytiri' },
    { imagePath: 'cast/cast02.jpeg', text: 'Norm spellman' },
    { imagePath: 'cast/cast03.jpeg', text: 'Kiri' },
    { imagePath: 'cast/cast04.jpeg', text: 'Quaritch' },
    { imagePath: 'cast/cast05.jpeg', text: 'Ronal' },
    { imagePath: 'cast/cast06.jpeg', text: 'TonoWari' },
    { imagePath: 'cast/cast07.jpeg', text: 'General Frances Ardmore' },
    { imagePath: 'cast/cast08.jpeg', text: 'Dr.lan Garvin' },
    { imagePath: 'cast/cast09.jpeg', text: 'Mick Scoresby' },
    { imagePath: 'cast/cast10.jpeg', text: 'Spider' },
    { imagePath: 'cast/cast11.jpeg', text: 'Neteyam' },
    { imagePath: 'cast/cast12.jpeg', text: 'Lo\'ak' },
    { imagePath: 'cast/cast13.jpeg', text: 'Tuktirey' },
    { imagePath: 'cast/cast14.jpeg', text: 'Tsireya' },
    { imagePath: 'cast/cast15.jpeg', text: 'Aonung' },
    { imagePath: 'cast/cast16.jpeg', text: 'Trudy Chacón' },
    { imagePath: 'cast/cast17.jpeg', text: 'Cpl. Lyle Wainfleet' },
  ];

  const positions = [
    { "--i": 0, top: 300 },
    { "--i": 20, top: 30 },
    { "--i": 40, bottom: 200 },
    { "--i": 60, top: 20 },
    { "--i": 80, bottom: 120 },
    { "--i": 100 },
    { "--i": 120, top: 30 },
    { "--i": 140 },
    { "--i": 160, bottom: 80 },
    { "--i": 180, top: 90 },
    { "--i": 200, bottom: 120 },
    { "--i": 220, bottom: 60 },
    { "--i": 240 },
    { "--i": 260, top: 30 },
    { "--i": 280, top: 80 },
    { "--i": 300 },
    { "--i": 320, top: 70 },
    { "--i": 340, bottom: 120 },
  ];

  const getRandomWidth = () => {
    return Math.floor(Math.random() * (160 - 80 + 1)) + 80; // Generates a random number between 50 and 160
  };

  return (
    <>
      <div className='bg-star'>
        {Array.from({ length: 50 }, (_, index) => (
          <div key={index} className="shooting-star"></div>
        ))}
      </div>

      <section className='page1'>
        <div className='satellites'>
          {satelliteData.map((data, index) => (
            <div key={index} className="satellite" style={{ ...positions[index], width: `${getRandomWidth()}px` }}>
              <img src={process.env.PUBLIC_URL + '/' + data.imagePath} alt={`Satellite ${index}`} />
              <p className="satellite-text">{data.text}</p>
            </div>
          ))}
        </div>
      </section>
    </>
  );
}

export default Page1;
